import React, { useState, useEffect } from 'react'
import { Row, Col, Image, Form, Input, Select, DatePicker, Switch, Button, Typography, Tooltip, Space, Spin, Upload } from 'antd'
import * as anticons from '@ant-design/icons'
import toast from 'react-hot-toast'
import dayjs from 'dayjs'
import moment from 'moment'

import { disabledDate, getCatalogos, getFormatoFecha } from './../../utils/Utils'

import UsuarioService from '../../service/UsuarioService'
const usuarioService = new UsuarioService()

const { Option } = Select

const User = (props) => {

  const { user, navigate } = props
  const { id } = user
  const [formUser] = Form.useForm()

  const [load, setLoad] = useState(true)
  const [isLoading, setIsLoading] = useState(false)
  const [isSaving, setIsSaving] = useState(false)
  const [usuario, setUsuario] = useState(null)
  const [currentlyWorking, setCurrentlyWorking] = useState(true)

  const [photoFile, setPhotoFile] = useState();

  const [fileList, setFileList] = useState([]);

  const propsUpload = {
    onRemove: (file) => {
      const index = fileList.indexOf(file);
      const newFileList = fileList.slice();
      newFileList.splice(index, 1);
      setFileList(newFileList);
      setPhotoFile(null);
    },
    beforeUpload: (file) => {
      setFileList([...fileList, file]);
      setPhotoFile(URL.createObjectURL(file));
      return false;
    },
    fileList,
  }

  const renderPaisValor = e => <Option key={e.catValor} value={e.catId}>
    <>
      <img
        src={`https://flagcdn.com/24x18/${e.catDescripcion.toLowerCase()}.png`}
        srcSet={`https://flagcdn.com/48x36/${e.catDescripcion.toLowerCase()}.png 2x,https://flagcdn.com/72x54/${e.catDescripcion.toLowerCase()}.png 3x`}
        width="24"
        height="18"
        alt={e.catNombre}></img>
      &nbsp;{
        e.catNombre
      }
    </>
  </Option>

  const renderPaisCode = e => <Option key={e.catValor} value={e.catValor}>
    <>
      +({e.catValor})&nbsp;
      <img
        src={`https://flagcdn.com/24x18/${e.catDescripcion.toLowerCase()}.png`}
        srcSet={`https://flagcdn.com/48x36/${e.catDescripcion.toLowerCase()}.png 2x,https://flagcdn.com/72x54/${e.catDescripcion.toLowerCase()}.png 3x`}
        width="24"
        height="18"
        alt={e.catDescripcion}></img>
    </>
  </Option>

  const handleUpload = async ({ usuId }) => {
    const datos = new FormData();
    datos.append('file', fileList[0])
    datos.append('usuId', usuId)
    try {
      const { data } = await usuarioService.upLoadPhoto({ datos })
      if (data && data.listObject.length > 0 && data.listObject[0].usuId) {
        toast.success('Profile updated successfully')
      }
    } catch (_) {
      toast.error('Upload failed')
    } finally {
      setIsSaving(false)
      setFileList([])
    }
  }

  const update = async (values) => {
    const usuario = {
      ...values
      , trabaja: currentlyWorking
      , codigotel: values.countryCode.catValor
      , sexoId: values.sexo.catId
      , paisId: values.pais.catId
      , estadoId: values.estado.catId
    }
    setIsSaving(true)
    try {
      const { data } = await usuarioService.set({ navigate, usuario })
      const usuarioActualizado = data.listObject[0]
      setUsuario(usuarioActualizado)
      if (fileList.length > 0) {
        handleUpload(usuarioActualizado)
      } else {
        setIsSaving(false)
        toast.success('Profile updated successfully')
      }
    }
    catch (error) {
      setIsSaving(false)
      toast.error("An error ocurred")
    }
  }

  const fetchData = async () => {
    setLoad(false)
    setIsLoading(true)
    try {
      const { data } = await usuarioService.get({ navigate, usuario: { usuId: id } })
      if (data) {
        const usuario = data.listObject[0]
        const newUsuario = {
          ...usuario
          , countryCode: {
            catValor: usuario.codigotel.replace('+', '')
          }
          , fechanac: moment(dayjs(usuario.fechanac.substring(0, 10), getFormatoFecha()).$d)
        }
        setUsuario(newUsuario)
        setCurrentlyWorking(newUsuario.trabaja)
      } else {
        toast.error('An error ocurred')
      }
    } catch (error) {
      const { response } = error
      toast.error(response.status === 409 ? response.data[0].messageText : 'An error ocurred')
    } finally {
      setIsLoading(false)
    }
  }

  useEffect(() => {
    if (load) {
      fetchData()
    }
  })

  return (
    <>
      <div className="grid">
        <div className="col-12">
          <div className="mgeCard">
            <div className="grid">
              <div className="col-4">
                <h3 style={{ padding: '0', margin: '0' }} className='mge-darkGreen'>
                  My Account
                </h3>
              </div>
              <div className="col-8" style={{ textAlign: 'right' }}>
                <Space>
                  <Button
                    type='default'
                    disabled={isLoading || isSaving}
                    onClick={() => {
                      window.history.go(-1)
                    }}
                  >
                    Cancel
                  </Button>
                  <Button
                    type='primary'
                    loading={isSaving}
                    disabled={isLoading || isSaving}
                    onClick={() => { formUser.submit() }}>Save</Button>
                </Space>
              </div>
            </div>
          </div>
        </div>
      </div>
      {
        isLoading ?
          <Row style={{ padding: '30px 10px' }}>
            <Col span={24} style={{ textAlign: 'center' }}>
              <Spin size='large' tip={
                <Typography.Paragraph style={{ padding: '10px 0' }}>
                  Loading...
                </Typography.Paragraph>
              } />
            </Col>
          </Row>
          :
          usuario && <Row justify='center' style={{ paddingTop: '20px' }}>
            <Col xs={24} sm={24} md={22} lg={20} xl={18} xxl={16}>
              <Form
                form={formUser}
                layout={'vertical'}
                initialValues={usuario}
                onFinish={update}
                disabled={isSaving}
              >
                <Row gutter={[10, 0]}>
                  <Col xs={24} sm={8} md={8} lg={8} xl={8} xxl={8}>
                    <Form.Item
                      name='file'
                      label='Upload your profile picture'
                    >
                      <Row gutter={[0, 10]}>
                        <Col span={24} style={{ textAlign: 'center' }}>
                          {
                            photoFile ?
                              <Image src={photoFile} width={128} height={128} />
                              :
                              usuario.imagen ?
                                <Image src={
                                  `/images/${usuario.imagen.split('/')[usuario.imagen.split('/').length - 1]}`
                                } width={128} height={128} />
                                :
                                <Image src='./../assets/photo_profile.png' width={128} height={128} />
                          }
                        </Col>
                        <Col span={24} style={{ textAlign: 'center' }}>
                          <Upload
                            multiple={false}
                            {...propsUpload}
                          >
                            <Button
                              type='primary'
                              icon={<anticons.UploadOutlined />}
                              disabled={
                                fileList.length > 0
                              }
                            >Select Photo</Button>
                          </Upload>
                        </Col>
                      </Row>
                    </Form.Item>
                  </Col>
                  <Col xs={24} sm={16} md={16} lg={16} xl={16} xxl={16}>
                    <Row gutter={[15, 0]}>
                      <Col xs={24} sm={12} md={12} lg={12} xl={12} xxl={12}>
                        <Form.Item
                          name='usuId'
                          label="ID"
                          rules={[
                            {
                              required: true,
                              message: 'Complete ID',
                            },
                          ]}
                          style={{ display: 'none' }}
                        >
                          <Input />
                        </Form.Item>
                        <Form.Item
                          name='nombres'
                          label="First Name"
                          rules={[
                            {
                              required: true,
                              message: 'Complete first Name',
                            },
                          ]}
                        >
                          <Input />
                        </Form.Item>
                      </Col>
                      <Col xs={24} sm={12} md={12} lg={12} xl={12} xxl={12}>
                        <Form.Item
                          name='apellidos'
                          label="Last Name"
                          rules={[
                            {
                              required: true,
                              message: 'Complete last Name',
                            },
                          ]}
                        >
                          <Input />
                        </Form.Item>
                      </Col>
                      <Col xs={24} sm={12} md={12} lg={12} xl={12} xxl={12}>
                        <Form.Item
                          name={['sexo', 'catId']}
                          label='Sex'
                          rules={[
                            {
                              required: true,
                              message: 'Select sex',
                            },
                          ]}
                        >
                          <Select
                            style={{
                              width: '100%',
                              display: 'inline-block'
                            }}
                            fieldNames={{
                              label: 'catNombre',
                              value: 'catId'
                            }}
                            showSearch
                            options={getCatalogos().generos}
                            optionFilterProp="catNombre"
                          />
                        </Form.Item>
                      </Col>
                      <Col xs={24} sm={12} md={12} lg={12} xl={12} xxl={12}>
                        <Form.Item
                          name='fechanac'
                          label="Date of birth"
                          rules={[
                            {
                              required: true,
                              message: 'Select date of birth',
                            },
                          ]}
                        >
                          <DatePicker style={{ width: '100%' }} disabledDate={disabledDate} />
                        </Form.Item>
                      </Col>
                    </Row>
                  </Col>
                  <Col xs={24} sm={12} md={8} lg={8} xl={8} xxl={8}>
                    <Form.Item
                      name='documento'
                      label={
                        <Space direction='horizontal'>
                          <>ID Number</>
                          <Tooltip
                            title={
                              <>
                                This is the unique identification number issued by the country of residence.
                              </>
                            }
                            placement='right'
                          >
                            <anticons.InfoCircleFilled />
                          </Tooltip>
                        </Space>
                      }
                      rules={[
                        {
                          required: true,
                          message: 'Complete ID Number',
                        },
                      ]}
                    >
                      <Input />
                    </Form.Item>
                  </Col>
                  <Col xs={24} sm={12} md={8} lg={8} xl={8} xxl={8}>
                    <Form.Item
                      name={['pais', 'catId']}
                      label="Country"
                      rules={[
                        {
                          required: true,
                          message: 'Select country',
                        },
                      ]}
                    >
                      <Select name='paisId'>
                        {
                          getCatalogos().paises.map(p => renderPaisValor(p))
                        }
                      </Select>
                    </Form.Item>
                  </Col>
                  <Col xs={24} sm={12} md={8} lg={8} xl={8} xxl={8}>
                    <Form.Item
                      name='ciudad'
                      label="City"
                      rules={[
                        {
                          required: true,
                          message: 'Complete city',
                        },
                      ]}
                    >
                      <Input />
                    </Form.Item>
                  </Col>
                  <Col xs={24} sm={12} md={8} lg={8} xl={8} xxl={8}>
                    <Form.Item
                      name={['countryCode', 'catValor']}
                      label={
                        <span style={{ padding: '0 0 2px 0' }}>
                          Country Code
                        </span>
                      }
                      rules={[
                        {
                          required: true,
                          message: 'Select country code',
                        }
                      ]}
                    >
                      <Select name='codigotel'>
                        {
                          getCatalogos().paises.map(p => renderPaisCode(p))
                        }
                      </Select>
                    </Form.Item>
                  </Col>
                  <Col xs={24} sm={12} md={8} lg={8} xl={8} xxl={8}>
                    <Form.Item
                      name='celular'
                      label={
                        <span style={{ padding: '0 0 2px 0' }}>
                          Phone Number
                        </span>
                      }
                      rules={[
                        {
                          required: true,
                          message: 'Complete phone number',
                        }
                      ]}
                    >
                      <Input />
                    </Form.Item>
                  </Col>
                  <Col xs={24} sm={12} md={8} lg={8} xl={8} xxl={8}>
                    <Form.Item
                      name='lugartrabajo'
                      label={
                        <Space direction='horizontal'>
                          <>Are you currently working?</>
                          <Tooltip
                            title={
                              <>
                                Please write the complete name of the institution.
                              </>
                            }
                            placement='right'
                          >
                            <anticons.InfoCircleFilled />
                          </Tooltip>
                          <Switch
                            checked={currentlyWorking}
                            onChange={(c) => { setCurrentlyWorking(c) }}
                          />
                        </Space>
                      }
                      rules={[
                        {
                          required: currentlyWorking,
                          message: 'If yes, please write the complete name of the institution.',
                        },
                      ]}
                    >
                      <Input name='lugartrabajo' disabled={!currentlyWorking} />
                    </Form.Item>
                  </Col>
                  <Col xs={24} sm={12} md={12} lg={12} xl={12} xxl={12}>
                    <Form.Item
                      name='email'
                      label="Email"
                      rules={[
                        {
                          required: true,
                          message: 'Complete email',
                        },
                        {
                          type: 'email',
                          message: 'Email is not valid'
                        }
                      ]}
                    >
                      <Input disabled />
                    </Form.Item>
                  </Col>
                  <Col xs={24} sm={12} md={12} lg={12} xl={12} xxl={12}>
                    <Form.Item
                      name='email2'
                      label="Additional Email"
                      rules={[
                        {
                          required: true,
                          message: 'Complete additional email',
                        },
                        {
                          type: 'email',
                          message: 'Additional email is not valid'
                        }
                      ]}
                    >
                      <Input />
                    </Form.Item>
                  </Col>
                  <Col xs={24} sm={12} md={12} lg={12} xl={12} xxl={12} style={{ display: 'none' }}>
                    <Form.Item
                      name={['estado', 'catId']}
                      label='State'
                      rules={[
                        {
                          required: true,
                          message: 'Select state',
                        },
                      ]}
                    >
                      <Select
                        style={{
                          width: '100%',
                          display: 'inline-block'
                        }}
                        fieldNames={{
                          label: 'catNombre',
                          value: 'catId'
                        }}
                        showSearch
                        options={getCatalogos().states}
                        optionFilterProp="catNombre"
                      />
                    </Form.Item>
                  </Col>
                </Row>
              </Form>
            </Col>
          </Row>
      }
    </>
  )
}

export default User