import React, { useEffect, useState } from 'react'
import { useHistory } from 'react-router-dom'
import { Row, Col, Typography } from 'antd'

import PostulantService from '../../service/PostulantService'
import { getDatosUsuario } from '../../utils/Utils'
import toast from 'react-hot-toast'

const postulantService = new PostulantService()

const Classes = () => {
    const navigate = useHistory()
    const [load, setLoad] = useState(true)

    const fetchData = async () => {
        setLoad(false)
        try {
            const { data } = await postulantService.getTraining({ navigate, postulant: { idUsuario: getDatosUsuario().usuarioId } })
            const postulant = data.listObject[0]
            localStorage.setItem(
                'mgeInformList',
                JSON.stringify(postulant.informeList)
            )
        } catch (error) {
            const { response } = error
            toast.error(response.status === 409 ? response.data[0].messageText : 'An error occurred')
        }
    }

    useEffect(() => {
        if (load) fetchData()
    })

    return (
        <>
            <Row>
                <Col span={24}>
                    <div style={
                        {
                            backgroundColor: '#ffffff',
                            padding: '15px 20px 6px 20px',
                            borderRadius: '15px',
                            marginBottom: '20px'
                        }
                    }>
                        <Row>
                            <Col>
                                <Row>
                                    <Col>
                                        <Typography.Title level={3} type='secondary'>
                                            Trainings
                                        </Typography.Title>
                                    </Col>
                                </Row>
                            </Col>
                        </Row>
                    </div>
                </Col>
            </Row>
        </>
    )
}

export default Classes