import React, { useState } from 'react'
import { Button, Checkbox, Form, Input, Select, Space, Typography } from 'antd'
import PasswordChecklist from 'react-password-checklist'

const { Paragraph, Text } = Typography

const RestorePassword = ({ user, resetPassword, isReseting, closeResetPassword }) => {

    const [password, setPassword] = useState('')
    const [validForm, setValidForm] = useState(false)
    const [sendNotification, setSendNotification] = useState(false)

    return (
        <>
            <Typography>
                <Paragraph>
                    <Text strong>
                        {user.nombres} {user.apellidos}
                    </Text>
                    <Text type='secondary'>
                        <br />{user.email}
                    </Text>
                </Paragraph>
            </Typography>
            <Form
                layout={'vertical'}
                initialValues={
                    { ...user, sendNotification }
                }
                onFinish={resetPassword}
                disabled={isReseting}
            >
                <Form.Item
                    style={{ display: 'none' }}
                    name='usuId'
                    label='Usuario'
                >
                    <Input />
                </Form.Item>
                <Form.Item
                    name='password'
                    label='Password'
                    rules={[
                        {
                            required: true,
                            message: 'Complete password'
                        }
                    ]}
                >
                    <Input.Password onChange={e => setPassword(e.target.value)} />
                </Form.Item>
                <PasswordChecklist
                    rules={["minLength", "specialChar", "number", "capital"]}
                    minLength={8}
                    value={password}
                    messages={{
                        minLength: "Password has more than 8 characters.",
                        specialChar: "Password has special characters.",
                        number: "Password has a number.",
                        capital: "Password has a capital letter.",
                    }}
                    onChange={(isValid) => { setValidForm(isValid) }}
                    style={{
                        padding: '0 0 20px 0',
                        display: validForm ? 'none' : 'block'
                    }}
                />
                <Form.Item
                    name='sendNotification'
                    valuePropName='checked'
                >
                    <Checkbox defaultChecked={false} onChange={e => setSendNotification(e.target.checked)}>Send notification</Checkbox>
                </Form.Item>
                <Form.Item
                    name='email'
                    label='Email'
                    rules={[
                        {
                            required: sendNotification,
                            message: 'Select email'
                        }
                    ]}
                >
                    <Select
                        fieldNames={{
                            label: 'value',
                            value: 'id'
                        }}
                        showSearch
                        options={[
                            {
                                id: user.email,
                                value: user.email
                            },
                            {
                                id: user.email2,
                                value: user.email2
                            }
                        ]}
                    />
                </Form.Item>
                <Space>
                    <Button onClick={closeResetPassword}
                        disabled={isReseting}
                    >Cancel</Button>
                    <Button type="primary"
                        htmlType='submit'
                        disabled={isReseting}
                        loading={isReseting}>
                        {
                            isReseting ? 'Reseting' : 'Reset Password'
                        }
                    </Button>
                </Space>
            </Form >
        </>
    )
}

export default RestorePassword