import React, { useState } from 'react'
import { Button, Form, Input, Space, Typography } from 'antd'
import PasswordChecklist from 'react-password-checklist'
import md5 from 'md5'

import UsuarioService from '../../service/UsuarioService'
import toast from 'react-hot-toast'
const usuarioService = new UsuarioService()

const { Paragraph, Text } = Typography

const RestorePasswordUser = ({ user, navigate }) => {

    const [passwordNew, setPasswordNew] = useState('')
    const [passwordNewConfirm, setPasswordNewConfirm] = useState('')
    const [isReseting, setIsReseting] = useState(false)
    const [validForm, setValidForm] = useState(false)

    const resetPassword = async (values) => {
        const usuario = {
            usuId: values.usuId,
            oldpassword: md5(values.password),
            password: md5(values.passwordNew)
        }
        console.log(usuario)
        setIsReseting(true)
        try {
            const { data } = await usuarioService.resetPassword({ navigate, usuario })
            if (data)
                toast.success('Password reset successfully')
            setTimeout(() => {
                localStorage.removeItem("mgeToken")
                localStorage.removeItem("mgeDatosUsuario")
                localStorage.removeItem("mgeInformList")
                navigate.push("/login")
                setIsReseting(false)
            }, 3000)
        } catch (error) {
            setIsReseting(false)
            const { response } = error
            toast.error(response.status === 409 ? response.data[0].messageText : "An error ocurred")
        }
    }

    const cancelResetPassword = () => {
        window.history.go(-1)
    }

    return (
        <>
            <Typography>
                <Paragraph>
                    <Text strong>
                        {user.nombres} {user.apellidos}
                    </Text>
                    <Text type='secondary'>
                        <br />{user.email}
                    </Text>
                </Paragraph>
            </Typography>
            <Form
                layout={'vertical'}
                initialValues={
                    { ...user }
                }
                onFinish={resetPassword}
                disabled={isReseting}
            >
                <Form.Item
                    style={{ display: 'none' }}
                    name='usuId'
                    label='Usuario'
                >
                    <Input />
                </Form.Item>
                <Form.Item
                    name='password'
                    label='Password'
                    rules={[
                        {
                            required: true,
                            message: 'Complete password'
                        }
                    ]}
                >
                    <Input.Password />
                </Form.Item>
                <Form.Item
                    name='passwordNew'
                    label='New Password'
                    rules={[
                        {
                            required: true,
                            message: 'Complete new password'
                        }
                    ]}
                >
                    <Input.Password onChange={e => setPasswordNew(e.target.value)} />
                </Form.Item>
                <Form.Item
                    name='passwordNewConfirm'
                    label='Confirm New Password'
                    rules={[
                        {
                            required: true,
                            message: 'Confirm new password'
                        }
                    ]}
                >
                    <Input.Password onChange={e => setPasswordNewConfirm(e.target.value)} />
                </Form.Item>
                <PasswordChecklist
                    rules={["minLength", "specialChar", "number", "capital", "match"]}
                    minLength={8}
                    value={passwordNew}
                    valueAgain={passwordNewConfirm}
                    messages={{
                        minLength: "Password has more than 8 characters.",
                        specialChar: "Password has special characters.",
                        number: "Password has a number.",
                        capital: "Password has a capital letter.",
                        match: "Passwords match."
                    }}
                    onChange={(isValid) => { setValidForm(isValid) }}
                    style={{
                        padding: '0 0 20px 0',
                        display: validForm ? 'none' : 'block'
                    }}
                />
                <Space>
                    <Button onClick={cancelResetPassword}
                        disabled={isReseting}
                    >Cancel</Button>
                    <Button type="primary"
                        htmlType='submit'
                        disabled={isReseting}
                        loading={isReseting}>
                        {
                            isReseting ? 'Reseting' : 'Reset Password'
                        }
                    </Button>
                </Space>
            </Form >
        </>
    )
}

export default RestorePasswordUser