import React, { useEffect, useState } from 'react'
import { useHistory, useParams, Link } from 'react-router-dom'
import { Col, Row, Button, Result, Form, Input, Spin, Typography, Image, Space, Tabs, Badge, Card, Alert } from 'antd'

import UsuarioService from '../../service/UsuarioService'
import TrainingService from '../../service/TrainingService'
import toast from 'react-hot-toast'

const usuarioService = new UsuarioService()
const trainingService = new TrainingService()

const UserInfo = () => {

    const navigate = useHistory()
    const { id } = useParams()
    const [load, setLoad] = useState(true)
    const [isLoading, setIsLoading] = useState(false)
    const [usuario, setUsuario] = useState(null)
    const [trainings, setTrainings] = useState([])
    const [error, setError] = useState(null)

    const renderTraining = ({ entEstado, entFechaFin, entFechaIni, entId, entModalidad, entNombre, entPais, numPostulantes }) =>
        <Card
            key={entId}
            bordered={false}
            style={{ marginBottom: '10px' }}

        >
            <Row>
                <Col xs={12} sm={6} md={6} lg={6} xl={6} xxl={6} style={{ paddingTop: '4px' }}>
                    <Typography.Text strong>
                        {entNombre}
                    </Typography.Text>
                </Col>
                <Col xs={12} sm={6} md={6} lg={6} xl={6} xxl={6} style={{ textAlign: 'center', paddingTop: '4px' }}>
                    <Typography.Text strong>
                        <Badge color={entEstado.catValor === 'ACTIVE' ? '#54873C' : entEstado.catValor === 'INACTIVE' ? 'red' : '#636466'} text={entEstado.catNombre} />
                    </Typography.Text>
                </Col>
                <Col xs={12} sm={6} md={6} lg={6} xl={6} xxl={6} style={{ paddingTop: '4px' }}>
                    <Typography.Text strong>
                        {entFechaIni.substring(0, 10).split('-').reverse().join('/')} - {entFechaFin.substring(0, 10).split('-').reverse().join('/')}
                    </Typography.Text>
                </Col>
                <Col xs={12} sm={6} md={6} lg={6} xl={6} xxl={6} style={{ textAlign: 'center' }}>
                    <Button type='primary'
                        onClick={() => {
                            navigate.push(`/traininginfo/${entId}`)
                        }}
                    >
                        View
                    </Button>
                </Col>
            </Row>
        </Card>

    const fetchTrainings = async () => {
        const trainer = { idUsuario: id }
        try {
            const { data } = await trainingService.getByTraining({ navigate, trainer })
            if (data) {
                setTrainings(data.listObject)
            } else {
                toast.error("An error ocurred")
            }
        } catch (error) {
            const { response } = error
            toast.error(response.status === 409 ? response.data[0].messageText : "An error ocurred")
        } finally {
            setIsLoading(false)
        }
    }

    const fetchData = async () => {
        setLoad(false)
        setError(null)
        setIsLoading(true)
        try {
            const { data } = await usuarioService.get({ navigate, usuario: { usuId: id } })
            if (data) {
                const usuario = data.listObject[0]
                const newUsuario = { ...usuario, fechanac: usuario.fechanac.substring(0, 10) }
                setUsuario(newUsuario)
                fetchTrainings()
            } else {
                setIsLoading(false)
                setError("An error ocurred")
            }
        } catch (error) {
            setIsLoading(false)
            const { response } = error
            setError(response.status === 409 ? response.data[0].messageText : "An error ocurred")
        }
    }

    useEffect(() => {
        if (load) fetchData()
    })

    if (error) {
        return (
            <Result
                status="warning"
                title={''}
                subTitle={''}
                extra={
                    <>
                        <h5>{error}</h5>
                        <Link to={'/dashboard'}>Regresar al inicio</Link>
                    </>
                }
            />
        )
    }

    return (
        <>
            {
                isLoading ?
                    <Row style={{ padding: '30px 10px' }}>
                        <Col span={24} style={{ textAlign: 'center' }}>
                            <Spin size='large' tip={
                                <Typography.Paragraph style={{ padding: '10px 0' }}>
                                    Loading...
                                </Typography.Paragraph>
                            } />
                        </Col>
                    </Row>
                    :
                    usuario && <>
                        <div className="grid">
                            <div className="col-12">
                                <div className="mgeCard">
                                    <div className="grid">
                                        <div className="col-12">
                                            <Space direction='horizontal'>
                                                <Button icon={<i className="fa-solid fa-chevron-left"></i>}
                                                    onClick={() => {
                                                        navigate.push(`/dashboard`)
                                                    }}
                                                ></Button>
                                                <h3 style={{ padding: '0', margin: '0' }} className='mge-darkGreen'>
                                                    {
                                                        `${usuario.nombres || 'Postulante'} ${usuario.apellidos || ''}`
                                                    }
                                                </h3>
                                            </Space>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <Tabs centered items={[
                            {
                                key: 1,
                                label: <Typography.Text style={{ fontSize: '1.1em', letterSpacing: '1px' }}>Information</Typography.Text>,
                                children: <>
                                    <Row gutter={[25, 25]} justify='center'>
                                        <Col xs={22} sm={20} md={6} lg={6} xl={6} xxl={4}>
                                            <Row gutter={[15, 15]} style={{ textAlign: 'center' }}>
                                                <Col span={24}>
                                                    {
                                                        usuario.imagen ?
                                                            <Image src={
                                                                `/images/${usuario.imagen.split('/')[usuario.imagen.split('/').length - 1]}`
                                                            } width={128} height={128} />
                                                            :
                                                            <Image src='./../assets/photo_profile.png' width={128} height={128} />
                                                    }
                                                </Col>
                                                <Col xs={12} sm={12} md={24} lg={24} xl={24} xxl={24} style={{ textAlign: 'left' }}>
                                                    <Typography.Paragraph>
                                                        <Typography.Text strong>
                                                            Role:
                                                        </Typography.Text>
                                                        <br />
                                                        {
                                                            usuario.perfiles && usuario.perfiles[0].perNombre
                                                        }
                                                    </Typography.Paragraph>
                                                </Col>
                                            </Row>
                                        </Col>
                                        <Col xs={22} sm={20} md={14} lg={14} xl={14} xxl={10}>
                                            <Form
                                                initialValues={usuario}
                                                layout='vertical'
                                            >
                                                <Row gutter={[25, 5]}>
                                                    <Col xs={24} sm={12} md={12} lg={12} xl={12} xxl={12}>
                                                        <Form.Item
                                                            name='nombres'
                                                            label="First Name"
                                                        >
                                                            <Input readOnly />
                                                        </Form.Item>
                                                    </Col>
                                                    <Col xs={24} sm={12} md={12} lg={12} xl={12} xxl={12}>
                                                        <Form.Item
                                                            name='apellidos'
                                                            label="Last Name"
                                                        >
                                                            <Input readOnly />
                                                        </Form.Item>
                                                    </Col>
                                                    <Col xs={24} sm={12} md={12} lg={12} xl={12} xxl={12}>
                                                        <Form.Item
                                                            name={['sexo', 'catNombre']}
                                                            label="Sex"
                                                        >
                                                            <Input readOnly />
                                                        </Form.Item>
                                                    </Col>
                                                    <Col xs={24} sm={12} md={12} lg={12} xl={12} xxl={12}>
                                                        <Form.Item
                                                            name='fechanac'
                                                            label="Date Birth"
                                                        >
                                                            <Input readOnly />
                                                        </Form.Item>
                                                    </Col>
                                                    <Col xs={24} sm={12} md={12} lg={12} xl={12} xxl={12}>
                                                        <Form.Item
                                                            name={['pais', 'catNombre']}
                                                            label="Country"
                                                        >
                                                            <Input readOnly />
                                                        </Form.Item>
                                                    </Col>
                                                    <Col xs={24} sm={12} md={12} lg={12} xl={12} xxl={12}>
                                                        <Form.Item
                                                            name={['ciudad']}
                                                            label="City"
                                                        >
                                                            <Input readOnly />
                                                        </Form.Item>
                                                    </Col>
                                                    <Col xs={24} sm={12} md={12} lg={12} xl={12} xxl={12}>
                                                        <Form.Item
                                                            name='codigotel'
                                                            label="Country Code"
                                                        >
                                                            <Input readOnly />
                                                        </Form.Item>
                                                    </Col>
                                                    <Col xs={24} sm={12} md={12} lg={12} xl={12} xxl={12}>
                                                        <Form.Item
                                                            name='celular'
                                                            label="Phone number"
                                                        >
                                                            <Input readOnly />
                                                        </Form.Item>
                                                    </Col>
                                                    <Col xs={24} sm={12} md={12} lg={12} xl={12} xxl={12}>
                                                        <Form.Item
                                                            name='documento'
                                                            label="ID Number"
                                                        >
                                                            <Input readOnly />
                                                        </Form.Item>
                                                    </Col>
                                                    <Col xs={24} sm={12} md={12} lg={12} xl={12} xxl={12}>
                                                        <Form.Item
                                                            name='lugartrabajo'
                                                            label="Currenty Working"
                                                        >
                                                            <Input readOnly />
                                                        </Form.Item>
                                                    </Col>
                                                    <Col xs={24} sm={12} md={12} lg={12} xl={12} xxl={12}>
                                                        <Form.Item
                                                            name='email'
                                                            label="Email"
                                                        >
                                                            <Input readOnly />
                                                        </Form.Item>
                                                    </Col>
                                                    <Col xs={24} sm={12} md={12} lg={12} xl={12} xxl={12}>
                                                        <Form.Item
                                                            name='email2'
                                                            label="Additional Email"
                                                        >
                                                            <Input readOnly />
                                                        </Form.Item>
                                                    </Col>
                                                </Row>
                                            </Form>
                                        </Col>
                                    </Row>
                                </>
                            }, {
                                key: 2,
                                label: <Typography.Text style={{ fontSize: '1.1em', letterSpacing: '1px' }}>Trainings</Typography.Text>,
                                children: <>
                                    {
                                        trainings && trainings.length > 0 ?
                                            <>
                                                {
                                                    trainings.map(t => renderTraining(t))
                                                }
                                            </>
                                            :
                                            <Alert
                                                description="Trainings not found."
                                                type="info"
                                                showIcon
                                                style={{ borderRadius: '12px', border: 'none' }}
                                            />
                                    }
                                </>
                            }]
                        }>
                        </Tabs>
                    </>
            }
            {/* <Row align='middle' gutter={10}>
                <Col>
                    <Link to={'/dashboard'}><anticons.ArrowLeftOutlined /></Link>
                </Col>
                <Col>
                    <Typography.Title
                        level={4}
                        style={{
                            margin: 0,
                        }}
                    >
                        Gestin de Usuario
                    </Typography.Title>
                </Col>
            </Row>
            <Divider></Divider> */}
            {/* {
                isLoading ?
                    <div className="p-10 text-center">
                        <Spin size="large" /><br />
                        <Typography.Text>Espere un momento...</Typography.Text>
                    </div> :
                    <Form
                        layout={'vertical'}
                        form={form}
                        initialValues={{
                            layout: 'vertical',
                        }}
                    >
                        <Row gutter={10}>
                            <Col xs={24} sm={12} md={12} lg={12} xl={12}>
                                <Form.Item label="Identificacin">
                                    <Input id="documento" name="documento" label='Identificacin' type='text' value={usuario.documento} onChange={onChange} />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={12} md={12} lg={12} xl={12}>
                                <Form.Item label="Tipo de Identificacin">
                                    <Select defaultValue={usuario.tipoDocumento} value={usuario.tipoDocumento} onChange={onChangeTipoIdentificacion}>
                                        {
                                            tiposIdentificacion && tiposIdentificacion.map(p => renderCatalogoValor(p))
                                        }
                                    </Select>
                                </Form.Item>
                            </Col>
                        </Row>
                        <Row gutter={10}>
                            <Col xs={24} sm={12} md={12} lg={12} xl={12}>
                                <Form.Item label="Nombres">
                                    <Input id="nombres" name="nombres" label='Nombres' type='text' value={usuario.nombres} onChange={onChange} />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={12} md={12} lg={12} xl={12}>
                                <Form.Item label="Apellidos">
                                    <Input id="apellidos" name="apellidos" label='Apellidos' type='text' value={usuario.apellidos} onChange={onChange} />
                                </Form.Item>
                            </Col>
                        </Row>
                        <Row gutter={10}>
                            <Col xs={24} sm={24} md={24} lg={24} xl={24}>
                                <Form.Item label="Direccin">
                                    <Input id="direccion" name="direccion" label='Direccin' type='text' value={usuario.direccion} onChange={onChange} />
                                </Form.Item>
                            </Col>
                        </Row>
                        <Row gutter={10}>
                            <Col xs={24} sm={12} md={12} lg={12} xl={12}>
                                <Form.Item label="Email">
                                    <Input id="email" name="email" label='Email' type='text' value={usuario.email} onChange={onChange} />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={12} md={12} lg={12} xl={12}>
                                <Form.Item label="Telfono">
                                    <Input id="telefono" name="telefono" label='Telfono' type='text' value={usuario.telefono} onChange={onChange} />
                                </Form.Item>
                            </Col>
                        </Row>
                        <Row gutter={10}>
                            <Col xs={24} sm={12} md={12} lg={12} xl={12}>
                                <Form.Item label="Login">
                                    <Input id="login" name="login" label='Login' type='text' value={usuario.login} onChange={onChange} disabled />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={12} md={12} lg={12} xl={12}>
                                <Form.Item label="Contrasea">
                                    <Input.Password id="password" name="password" label='Contrasea' value={usuario.password} onChange={onChange} />
                                </Form.Item>
                            </Col>
                        </Row>
                        <Row gutter={10}>
                            <Col xs={24} sm={12} md={12} lg={12} xl={12}>
                                <Form.Item label="Estado">
                                    <Select
                                        defaultValue={usuario.estado.catValor}
                                        value={usuario.estado.catValor}
                                        onChange={onChangeEstado}
                                    >
                                        {
                                            estados && estados.map(e => renderEstado(e))
                                        }
                                    </Select>
                                </Form.Item>
                            </Col>
                        </Row>
                        <Form.Item>
                            <Button type="primary" onClick={(e) => guardar(e)} disabled={isSaving}
                                loading={isSaving}
                            >
                                {isSaving ? 'Guardando...' : 'Guardar'}
                            </Button>
                        </Form.Item>
                    </Form>
            } */}
        </>
    )
}

export default UserInfo