import React from 'react'
import { Col, Row, Space, Button, Form, Select, InputNumber, Input, Switch } from 'antd'
import { getCatalogos } from '../../utils/Utils'

const { Option } = Select

const renderPaisValor = e => <Option key={e.catValor} value={e.catId}>
    <>
        <img
            alt={e.catNombre}
            src={`https://flagcdn.com/24x18/${e.catDescripcion.substr(0, 2).toLowerCase()}.png`}
            srcSet={`https://flagcdn.com/48x36/${e.catDescripcion.substr(0, 2).toLowerCase()}.png 2x,https://flagcdn.com/72x54/${e.catDescripcion.substr(0, 2).toLowerCase()}.png 3x`}
            width="24"
            height="18"
        ></img>
        &nbsp;{
            e.catNombre
        }
    </>
</Option>

const Beneficiary = ({ type, beneficiary, saveBeneficiary, isSaving, closeBeneficiary }) => {
    const [formBeneficiary] = Form.useForm()

    if (type === 1)
        return (
            <>
                {
                    beneficiary &&
                    <>
                        <Form
                            form={formBeneficiary}
                            layout={'vertical'}
                            initialValues={beneficiary}
                            onFinish={saveBeneficiary}
                            disabled={isSaving || beneficiary.editDisabled}
                        >
                            <div style={{ display: 'none' }}>
                                <Form.Item
                                    name={['benId']}
                                    label='ID'
                                >
                                    <Input />
                                </Form.Item>
                                <Form.Item
                                    name={['idTipo']}
                                    label='Type Person'
                                    rules={[
                                        {
                                            required: true
                                        }
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                                <Form.Item
                                    name={['idCurso']}
                                    label='Course'
                                    rules={[
                                        {
                                            required: true
                                        }
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                                <Form.Item
                                    name={['idPostulante']}
                                    label='Postulante'
                                    rules={[
                                        {
                                            required: true
                                        }
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </div>
                            <Form.Item
                                name={['benDocumento']}
                                label='ID Number'
                                rules={[
                                    {
                                        required: true,
                                        message: 'Complete de ID Number'
                                    }
                                ]}
                            >
                                <Input />
                            </Form.Item>
                            <Form.Item
                                name={['benNombres']}
                                label='Name'
                                rules={[
                                    {
                                        required: true,
                                        message: 'Complete Name'
                                    }
                                ]}
                            >
                                <Input />
                            </Form.Item>
                            <Form.Item
                                name={['idPais']}
                                label="Country"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Select Country'
                                    },
                                ]}
                            >
                                <Select name='idPais'>
                                    {
                                        getCatalogos().paises.map(p => renderPaisValor(p))
                                    }
                                </Select>
                            </Form.Item>
                            <Form.Item
                                name={['ciudad']}
                                label='City'
                                rules={[
                                    {
                                        required: true,
                                        message: 'Complete City'
                                    }
                                ]}
                            >
                                <Input />
                            </Form.Item>
                            <Form.Item
                                name={['idSexo']}
                                label='Sex'
                                rules={[
                                    {
                                        required: true,
                                        message: 'Select Sex'
                                    },
                                ]}
                            >
                                <Select
                                    style={{
                                        width: '100%',
                                        display: 'inline-block'
                                    }}
                                    fieldNames={{
                                        label: 'catNombre',
                                        value: 'catId'
                                    }}
                                    showSearch
                                    options={getCatalogos().generos}
                                    optionFilterProp="catNombre"
                                />
                            </Form.Item>
                            <Form.Item
                                name='benTrabaja'
                                label='Are you currently working?'
                                valuePropName='checked'
                                rules={[
                                    {
                                        required: true
                                    },
                                ]}
                            >
                                <Switch />
                            </Form.Item>
                            <Form.Item
                                name={['idNivelInt']}
                                label='Level Instruction'
                                rules={[
                                    {
                                        required: true,
                                        message: 'Complete Level Instruction'
                                    },
                                ]}
                            >
                                <Select
                                    style={{
                                        width: '100%',
                                        display: 'inline-block'
                                    }}
                                    fieldNames={{
                                        label: 'catNombre',
                                        value: 'catId'
                                    }}
                                    showSearch
                                    options={getCatalogos().educationLevels}
                                    optionFilterProp="catNombre"
                                />
                            </Form.Item>
                        </Form>
                        <Space>
                            <Button onClick={closeBeneficiary}
                                disabled={isSaving}
                            >Cancel</Button>
                            <Button type="primary"
                                disabled={isSaving || beneficiary.editDisabled}
                                loading={isSaving}
                                onClick={() => {
                                    formBeneficiary.submit()
                                }}>
                                {
                                    isSaving ? 'Saving' : 'Save'
                                }
                            </Button>
                        </Space>
                    </>
                }
            </>
        )

    if (type === 2)
        return (
            <>
                {
                    beneficiary &&
                    <>
                        <Form
                            form={formBeneficiary}
                            layout={'vertical'}
                            initialValues={beneficiary}
                            onFinish={saveBeneficiary}
                            disabled={isSaving || beneficiary.editDisabled}
                        >
                            <div style={{ display: 'none' }}>
                                <Form.Item
                                    name={['benId']}
                                    label='ID'
                                >
                                    <Input />
                                </Form.Item>
                                <Form.Item
                                    name={['idTipo']}
                                    label='Type Person'
                                    rules={[
                                        {
                                            required: true
                                        }
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                                <Form.Item
                                    name={['idCurso']}
                                    label='Course'
                                    rules={[
                                        {
                                            required: true
                                        }
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                                <Form.Item
                                    name={['idPostulante']}
                                    label='Postulante'
                                    rules={[
                                        {
                                            required: true
                                        }
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </div>
                            <Form.Item
                                name={['benDocumento']}
                                label='ID Number'
                                rules={[
                                    {
                                        required: true,
                                        message: 'Complete ID Number'
                                    }
                                ]}
                            >
                                <Input />
                            </Form.Item>
                            <Form.Item
                                name={['benNombres']}
                                label='Name'
                                rules={[
                                    {
                                        required: true,
                                        message: 'Complete Name'
                                    }
                                ]}
                            >
                                <Input />
                            </Form.Item>
                            <Form.Item
                                name={['idPais']}
                                label="Country"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Complete Country'
                                    },
                                ]}
                            >
                                <Select name='idPais'>
                                    {
                                        getCatalogos().paises.map(p => renderPaisValor(p))
                                    }
                                </Select>
                            </Form.Item>
                            <Form.Item
                                name={['ciudad']}
                                label='City'
                                rules={[
                                    {
                                        required: true,
                                        message: 'Complete City'
                                    }
                                ]}
                            >
                                <Input />
                            </Form.Item>
                            <Form.Item
                                name={['idSexo']}
                                label='Sex'
                                rules={[
                                    {
                                        required: true,
                                        message: 'Select Sex'
                                    },
                                ]}
                            >
                                <Select
                                    style={{
                                        width: '100%',
                                        display: 'inline-block'
                                    }}
                                    fieldNames={{
                                        label: 'catNombre',
                                        value: 'catId'
                                    }}
                                    showSearch
                                    options={getCatalogos().generos}
                                    optionFilterProp="catNombre"
                                />
                            </Form.Item>
                            <Form.Item
                                name={['benIdEmpresa']}
                                label='Company ID'
                                rules={[
                                    {
                                        required: true,
                                        message: 'Complete Company ID'
                                    }
                                ]}
                            >
                                <Input />
                            </Form.Item>
                            <Form.Item
                                name={['benNomEmpresa']}
                                label='Company Name'
                                rules={[
                                    {
                                        required: true,
                                        message: 'Complete Company Name'
                                    }
                                ]}
                            >
                                <Input />
                            </Form.Item>
                            <Form.Item
                                name={['benCargo']}
                                label='Job Title'
                                rules={[
                                    {
                                        required: true,
                                        message: 'Complete Job Title'
                                    }
                                ]}
                            >
                                <Input />
                            </Form.Item>
                            <Form.Item
                                name={['benNumPer']}
                                label='Total Collaborators'
                                rules={[
                                    {
                                        required: true,
                                        message: 'Complete Total Collaborators'
                                    }
                                ]}
                            >
                                <InputNumber
                                    style={{
                                        width: '100%',
                                        display: 'inline-block'
                                    }}
                                />
                            </Form.Item>
                            <Row gutter={[10]}>
                                <Col span={12}>
                                    <Form.Item
                                        name={['benHombres']}
                                        label='Men'
                                        rules={[
                                            {
                                                required: true,
                                                message: 'Number of men'
                                            }
                                        ]}
                                    >
                                        <InputNumber
                                            style={{
                                                width: '100%',
                                                display: 'inline-block'
                                            }}
                                        />
                                    </Form.Item>
                                </Col>
                                <Col span={12}>
                                    <Form.Item
                                        name={['benMujeres']}
                                        label='Women'
                                        rules={[
                                            {
                                                required: true,
                                                message: 'Number of women'
                                            }
                                        ]}
                                    >
                                        <InputNumber
                                            style={{
                                                width: '100%',
                                                display: 'inline-block'
                                            }}
                                        />
                                    </Form.Item>
                                </Col>
                            </Row>
                        </Form>
                        <Space>
                            <Button onClick={closeBeneficiary}
                                disabled={isSaving}
                            >Cancel</Button>
                            <Button type="primary"
                                disabled={isSaving || beneficiary.editDisabled}
                                loading={isSaving}
                                onClick={() => {
                                    formBeneficiary.submit()
                                }}>
                                {
                                    isSaving ? 'Saving' : 'Save'
                                }
                            </Button>
                        </Space>
                    </>
                }
            </>
        )

}

export default Beneficiary