import axios from "axios"
import { getAutenticarUrl, getAutorizarUrl, updatePassword, recoveryPassword, validateUserUuid } from "../utils/urls"
import { getCulture } from "../utils/Internationalization"

const config = { headers: { "Accept-Language": getCulture() } }

export default class CuentaService {
    autenticar = (props) => {
        const { credenciales } = props
        return axios.post(getAutenticarUrl(), credenciales, config)
    }
    autorizar = (props) => {
        const { credenciales } = props
        return axios.post(getAutorizarUrl(), credenciales, config)
    }
    validateResetPassword = ({ datos }) => (axios.post(validateUserUuid(), datos, config))
    recoveryPassword = (props) => {
        const { datos } = props
        return axios.post(recoveryPassword(), datos, config)
    }
    updatePassword = (props) => {
        const { datos } = props
        return axios.patch(updatePassword(), datos, config)
    }
}