import React, { useState, useEffect } from 'react'
import { useHistory } from 'react-router-dom'
import { Row, Col, Typography, Button, Card, Statistic, Table, Form, Spin, Tooltip } from 'antd'
import * as anticons from '@ant-design/icons'

import UsuarioService from '../../service/UsuarioService'
import toast from 'react-hot-toast'

const usuarioService = new UsuarioService()

const tableProps = {
    bordered: false,
    loading: false,
    title: '',
    showHeader: true,
    size: 'medium',
    pagination: {
        position: ['none', 'bottomRight']
    }
};

const columnas = [
    {
        title: '',
        render: (_, r) => (r.imagen && <img alt='' src={`/images/${r.imagen.split('/')[r.imagen.split('/').length - 1]}`} width={32} height={32} />) || <img alt='' src='./assets/user.png' width={32} height={32} />
    },
    {
        title: 'Name',
        dataIndex: 'nombres',
        render: (_, r) => `${r.nombres}`
    },
    {
        title: 'Last Name',
        dataIndex: 'apellidos',
        render: (_, r) => `${r.apellidos}`
    },
    {
        title: 'Country',
        dataIndex: 'pais',
        render: (_, { pais }) => <div style={{ textAlign: 'center' }}>
            <Tooltip title={pais.catDescripcion}>
                <img
                    src={`https://flagcdn.com/32x24/${pais.catDescripcion.toLowerCase()}.png`}
                    srcSet={`https://flagcdn.com/32x24/${pais.catDescripcion.toLowerCase()}.png 2x,https://flagcdn.com/96x72/${pais.catDescripcion.toLowerCase()}.png 3x`}
                    width="32"
                    height="24"
                    alt={pais.catDescripcion}></img>
            </Tooltip>
        </div>,
        responsive: ['md', 'lg', 'xl', 'xxl']
    },
    {
        title: <div style={{ textAlign: 'center' }}>Role</div>,
        dataIndex: 'role',
        render: (_, { perfiles }) => <div style={{ textAlign: 'center' }}>
            {perfiles[0].perNombre}
        </div>,
        responsive: ['md', 'lg', 'xl', 'xxl']
    },
    {
        title: 'Actions',
        render: (_, record) => <Button type='primary' href={`/#/user/${record.usuId}`}>View</Button>
    }
]

const statistics = [
    {
        id: 1,
        props: {
            bordered: false,
            hoverable: true,
            style: {
                background: '#54873C',
                color: '#f0f0f0'
            }
        },
        data: {
            title: <Typography.Text style={{ color: '#ffffff' }}>TRAINER OF TRAINERS</Typography.Text>,
            value: 10,
            precision: 2,
            prefix: <anticons.UserOutlined />,
            suffix: '%'
        },
        description: <>120 Users</>
    },
    {
        id: 2,
        props: {
            bordered: false,
            hoverable: true,
            style: {
                background: '#70B450',
                color: '#f0f0f0'
            }
        },
        data: {
            title: <Typography.Text style={{ color: '#ffffff' }}>TRAINER</Typography.Text>,
            value: 10,
            precision: 2,
            prefix: <anticons.UserOutlined />,
            suffix: '%'
        },
        description: <>120 Users</>
    },
    {
        id: 3,
        props: {
            bordered: false,
            hoverable: true,
            style: {
                background: '#385A28',
                color: '#f0f0f0'
            }
        },
        data: {
            title: <Typography.Text style={{ color: '#ffffff' }}>APPLICANTS</Typography.Text>,
            value: 80,
            precision: 2,
            prefix: <anticons.UserOutlined />,
            suffix: '%'
        },
        description: <>120 Users</>
    },
    {
        id: 4,
        props: {
            bordered: false,
            hoverable: true,
            style: {
                background: '#636466',
                color: '#f0f0f0'
            }
        },
        data: {
            title: <Typography.Text style={{ color: '#ffffff' }}>BENEFICIARIES</Typography.Text>,
            value: 150,
            precision: 0,
            prefix: <anticons.UserOutlined />,
            suffix: ''
        },
        description: <>&nbsp;</>
    }
]

const Dashboard = () => {

    const navigate = useHistory()
    const [load, setLoad] = useState(true)
    const [isLoading, setIsLoading] = useState(false)
    const [users, setUsers] = useState([])

    const renderStatistics = ({ id, props, data, description }) =>
        <Col
            key={id}
            xs={12} sm={6} md={6} lg={6} xl={6} xxl={6}
        >
            <Card
                {...props}
            >
                <Statistic
                    {...data}
                    valueStyle={{
                        color: '#ffffff',
                    }}
                />
                {description}
            </Card>
        </Col>


    const fetchData = async () => {
        setLoad(false)
        setIsLoading(true)
        try {
            const { data } = await usuarioService.getLessPostulants({ navigate })
            if (data) {
                const usuarios = data.listObject
                setUsers(usuarios)
            } else {
                toast.error("An error ocurred")
            }
        } catch (error) {
            const { response } = error
            toast.error(response.status === 409 ? response.data[0].messageText : "An error occurred")
        } finally {
            setIsLoading(false)
        }
    }

    useEffect(() => {
        if (load) fetchData()
    })

    return (
        <>
            <Row gutter={[15, 15]}>
                {
                    statistics && statistics.map(s => renderStatistics(s))
                }
            </Row>
            <Row justify='end'>
                <Col style={{ padding: '15px 0' }}>
                    <Form
                        layout='inline'
                    >
                        <Form.Item>
                            <Button type='default'
                                onClick={() => fetchData()}
                            >
                                Refresh
                            </Button>
                        </Form.Item>
                    </Form>
                </Col>
            </Row>
            {
                isLoading ?
                    <Row style={{ padding: '30px 10px' }}>
                        <Col span={24} style={{ textAlign: 'center' }}>
                            <Spin size='large' tip={
                                <Typography.Paragraph style={{ padding: '10px 0' }}>
                                    Loading...
                                </Typography.Paragraph>
                            } />
                        </Col>
                    </Row>
                    :
                    <Row>
                        <Col span={24}>
                            <Table
                                {...tableProps}
                                rowKey='usuId'
                                columns={columnas}
                                dataSource={users}>
                            </Table>
                        </Col>
                    </Row>
            }
        </>
    )
}

export default Dashboard